<template>
    <div class="page">
        <h3 class="title"> base64 </h3>
        <div class="p-upload"
             :class="{'is-dragover':dragover}"
             @drop.prevent="onDrop"
             @dragover.prevent="onDragOver"
             @dragleave.prevent="onDragLeave"
             @click="handleClick"
            >
            <svg class="p-icon" viewBox="0 0 1024 1024" version="1" xmlns="http://www.w3.org/2000/svg" width="200" height="200"><path d="M737 436a174 174 0 0 1 172 172 172 172 0 0 1-172 172c-69 1-69 107 0 106 152-2 276-125 278-278S886 332 737 330c-69-1-69 105 0 106zM285 779a174 174 0 0 1-172-172 172 172 0 0 1 172-172c68-1 69-106 0-106A282 282 0 0 0 7 607a281 281 0 0 0 278 278c69 1 68-105 0-106z" fill="#4A5699"></path><path d="M340 384a174 174 0 0 1 172-172 172 172 0 0 1 172 172c1 68 106 68 106 0a282 282 0 0 0-278-278 281 281 0 0 0-278 278c-1 68 105 68 106 0z" fill="#C45FA0"></path><path d="M545 473c17 17 17 43 0 60L422 656a42 42 0 0 1-60-60l123-123c17-16 43-16 60 0z" fill="#F39A2B"></path><path d="M485 473c17-16 44-16 60 0l123 123a42 42 0 0 1-60 60L485 533a42 42 0 0 1 0-60z" fill="#F39A2B"></path><path d="M514 634c24 0 43 20 43 43v179a43 43 0 0 1-86 0V677c0-23 19-43 43-43z" fill="#E5594F"></path></svg>
            <div class="p-info"></div>
            <input
                 type="file"
                 ref="fileInput"
                 class="p-file"
                 accept="image/jpeg,image/png,image/jpg"
                 @change="addFile"
            >
        </div>
        <h4 class="title2" v-if="this.imgUrl">  </h4>
        <div class="img-con" v-if="this.imgUrl">
            <img :src="imgUrl" alt="" class="p-img">
            <div class="img-info">{{this.fileList[0]['name']}}</div>
        </div>
        <h4 class="title2" v-if="this.code"> base64 </h4>
        <div class="code" v-if="this.code">
             <code>{{code}}</code>
        </div>
    </div>
</template>

<script>
    export default {
        name: "index",
        data(){
            return{
                dragover:false,
                fileList:[],
                imgUrl:'',
                code:'',
            }
        },
        methods:{
            onDrop(e){
               this.dragover = false;
               const accept=['.jpg','.png','.jpeg'];
               [].slice.call(e.dataTransfer.files).filter(file=>{
                   const { type,name } = file;
                   const extension = `.${name.split('.').pop()}`
                   if(accept.indexOf(extension) != -1 ){
                       this.fileList = [file];
                       this.setImage();
                   }else{
                       console.log('')
                   }
               })
            },
            onDragOver(){
               this.dragover = true;
            },
            onDragLeave(){
               this.dragover = false;
            },
            addFile({target:{files}}){
               this.fileList = [files[0]]
               this.setImage();
            },
            handleClick(){
                this.$refs.fileInput.value = null;
                this.$refs.fileInput.click();
            },
            setImage(){
                if(this.fileList.length > 0){
                    this.imgUrl =  URL.createObjectURL(this.fileList[0]);

                    const that = this;
                    //base64
                    //
                    const reader = new FileReader();
                    reader.onload = function (ev) {
                        //base64
                        that.code = ev.target.result;
                    }
                    //data:url
                    reader.readAsDataURL(this.fileList[0])
                }
            }
        }
    }
</script>

